<template>
  <div class = "table">
    <div class = "crumbs">
      <el-breadcrumb separator = "/">
        <el-breadcrumb-item><i class = "el-icon-lx-cascades"></i>前台账号管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class = "container">
      <div class = "handle-box">
        <!--<el-button type="primary" icon="delete" class="handle-del mr10" @click="delAll">批量删除</el-button>-->
        <!--<el-select v-model="select_cate" placeholder="筛选省份" class="handle-select mr10">-->
        <!--    <el-option key="1" label="广东省" value="广东省"></el-option>-->
        <!--    <el-option key="2" label="湖南省" value="湖南省"></el-option>-->
        <!--</el-select>-->
        <el-button type = "primary" icon = "el-icon-plus" @click = "add()">新增管理员</el-button>
        <!--<el-input v-model = "select_word" placeholder = "筛选关键词" class = "handle-input mr10"></el-input>-->
        <!--<el-button type = "primary" icon = "search" @click = "search">搜索</el-button>-->
      </div>
      <el-table :data = "tableData" border class = "table" ref = "multipleTable"
        @selection-change = "handleSelectionChange">
        <el-table-column type = "selection" width = "55" align = "center"></el-table-column>
        <el-table-column prop = "createtime" label = "日期" sortable>
        </el-table-column>
        <el-table-column prop = "username" label = "姓名">
        </el-table-column>
        <el-table-column prop = "name" label = "账号">
        </el-table-column>
        <el-table-column label = "操作" width = "180" align = "center">
          <template slot-scope = "scope">
            <el-button type = "text" icon = "el-icon-edit" @click = "handleEdit(scope.$index, scope.row)">修改密码
            </el-button>
            <el-button type = "text" icon = "el-icon-delete" class = "red"
              @click = "handleDelete(scope.$index, scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class = "pagination">
        <el-pagination
          background
          :pager-count = "7"
          @prev-click = "previousPage"
          @next-click = "nextClick"
          @current-change = "handleCurrentPageChange"
          layout = "prev, pager, next"
          :total = "pages*10">
        </el-pagination>
      </div>
    </div>
    <!--添加前台账号-->
    <el-dialog :title = "formAdd.id!=undefined?'编辑':'新增'" autocomplete = "on" :visible.sync = "batchVisible"
      width = "500px"
      center>
      <el-form ref = "form" :model = "formAdd" :rules = "rules">
        <el-form-item label = "姓名" prop = "username">
          <el-input v-model = "formAdd.username" type = "text" autocomplete = "off" class = "mz_wb"></el-input>
        </el-form-item>
        <el-form-item label = "账号" prop = "name">
          <el-input type = "text" v-model = "formAdd.name" autocomplete = "off" class = "mz_wb"></el-input>
        </el-form-item>
        <el-form-item label = "密码" prop = "pwd">
          <el-input v-model = "formAdd.pwd" type = "text" autocomplete = "off" class = "mz_wb"></el-input>
        </el-form-item>
      </el-form>
      <span slot = "footer" class = "dialog-footer">
          <el-button @click = "batchVisible = false">取 消</el-button>
          <el-button type = "primary" @click = "addUser()">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑弹出框 -->
    <el-dialog title = "编辑" :visible.sync = "editVisible" width = "30%">
      <el-form ref = "formchange" :model = "formchange" label-width = "60px">
        <el-form-item label = "账号">
          <el-input v-model = "formchange.name"></el-input>
        </el-form-item>
        <el-form-item label = "旧密码">
          <el-input v-model = "formchange.oldpwd"></el-input>
        </el-form-item>
        <el-form-item label = "新密码">
          <el-input v-model = "formchange.newpwd"></el-input>
        </el-form-item>
      </el-form>
      <span slot = "footer" class = "dialog-footer">
                <el-button @click = "editVisible = false">取 消</el-button>
                <el-button type = "primary" @click = "saveEdit">确 定</el-button>
            </span>
    </el-dialog>
    <!-- 删除提示框 -->
    <el-dialog title = "提示" :visible.sync = "delVisible" width = "300px" center>
      <div class = "del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot = "footer" class = "dialog-footer">
                <el-button @click = "delVisible = false">取 消</el-button>
                <el-button type = "primary" @click = "deleteRow">确 定</el-button>
            </span>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'basetable',
    data() {
      return {
        //url: './static/vuetable.json',
        tableData: [],
        cur_page: 1, // 分页器
        pages: '', // 分页器计算
        multipleSelection: [],
        select_cate: '',
        select_word: '',
        del_list: [],
        is_search: false,
        editVisible: false,
        delVisible: false,
        form: {
          name: '',
          date: '',
          address: ''
        },
        idx: -1,
        //  添加前台账号
        radio: '',
        formAdd: {},
        batchVisible: false,
        rules: {
          name: [{
            required: true,
            message: '请填写用户名',
            trigger: 'blur'
          }],
          pwd: [{
            required: true,
            message: '请填写密码',
            trigger: 'blur'
          }],
          username: [{
            required: true,
            message: '请填写名称',
            trigger: 'blur'
          }]
        },
        //  编辑用户修改密码
        formchange: {},

      }
    },
    created() {
      this.getData();
    },
    computed: {
      data() {
        return this.tableData.filter((d) => {
          let is_del = false;
          for (let i = 0; i < this.del_list.length; i++) {
            if (d.name === this.del_list[i].name) {
              is_del = true;
              break;
            }
          }
          if (!is_del) {
            if (d.address.indexOf(this.select_cate) > -1 &&
              (d.name.indexOf(this.select_word) > -1 ||
                d.address.indexOf(this.select_word) > -1)
            ) {
              return d;
            }
          }
        })
      }
    },
    methods: {
      //添加前台账号
      add() {
        this.formAdd = {};
        this.radio = '';
        this.batchVisible = true;
      },
      //添加账号确定
      addUser() {
        let params = {
          username: this.formAdd.username,
          name: this.formAdd.name,
          pwd: this.$md5(this.formAdd.pwd)
        }
        this.$axios.post('/api/admin/user/useradd', this.$qs.stringify(params), {}).then(res => {
          console.log(res);
          this.batchVisible = false;
          if (res.data.msg == '存在同名用户') {
            this.$message.success('存在相同账号名');
          } else {
            this.getData()
            this.$message.success('添加成功');
          }
        }).catch(err => {
          console.log(err);
        })
      },
      // 分页导航
      handleCurrentChange(val) {
        this.cur_page = val;
        this.getData();
      },
      getData() {
        // 开发环境使用 easy-mock 数据，正式环境使用 json 文件
        //if (process.env.NODE_ENV === 'development') {
        //    this.url = '/ms/table/list';
        //};
        //this.$axios.post(this.url, {
        //    page: this.cur_page
        //}).then((res) => {
        //    this.tableData = res.data.list;
        //})
        let params = {
          page: this.cur_page,
          list_rows: 12
        }
        this.$axios.post('/api/admin/user/usergetalllist', this.$qs.stringify(params), {}).then(res => {
          this.tableData = res.data.data.data;
          this.pages = Math.ceil(res.data.data.total / res.data.data.listRows)
          console.log(this.tableData);
          console.log(res);
        }).catch(err => {
          console.log(err);
        })
      },
      //搜索
      search() {
        this.is_search = true;
      },
      formatter(row, column) {
        return row.address;
      },
      filterTag(value, row) {
        return row.tag === value;
      },
      handleEdit(index, row) {
        this.idx = index;
        const item = this.tableData[index];
        this.form = {
          name: item.name,
          date: item.date,
          address: item.address
        }
        this.editVisible = true;
      },
      handleDelete(index, row) {
        this.idx = index;
        this.delVisible = true;

      },
      delAll() {
        const length = this.multipleSelection.length;
        let str = '';
        this.del_list = this.del_list.concat(this.multipleSelection);
        for (let i = 0; i < length; i++) {
          str += this.multipleSelection[i].name + ' ';
        }
        this.$message.error('删除了' + str);
        this.multipleSelection = [];
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      // 保存编辑
      saveEdit() {
        let params = {
          id: this.tableData[this.idx].id,
          name: this.formchange.name,
          oldpwd: this.$md5(this.formchange.oldpwd),
          pwd: this.$md5(this.formchange.newpwd)
        }
        this.$axios.post('/api/admin/user/userchange', this.$qs.stringify(params), {}).then(res => {
          console.log(res);
          this.formchange = {}
          this.editVisible = false;
          this.$message.success(`修改第 ${this.idx + 1} 行成功`);
        }).catch(err => {
          console.log(err);
        })
        //this.$set(this.tableData, this.idx, this.form);
        //this.editVisible = false;
        //this.$message.success(`修改第 ${this.idx + 1} 行成功`);
      },
      // 确定删除
      deleteRow() {
        let params = {
          id: this.tableData[this.idx].id,
        }
        this.$axios.post('/api/admin/user/userdelete', this.$qs.stringify(params), {}).then(res => {
          console.log(res);
          this.$message.success('删除成功');
          this.delVisible = false;
          this.getData()
        }).catch(err => {
          console.log(err);
        })
        //this.tableData.splice(this.idx, 1);
        //this.$message.success('删除成功');
        //this.delVisible = false;
      },
      //分页器
      //点击上一页
      previousPage() {
        this.cur_page--
        this.getData()
      },
      //点击下一页
      nextClick() {
        this.cur_page++
        this.getData()
      },
      //当点击分页器页数的时候
      handleCurrentPageChange(val) {
        this.cur_page = val
        this.getData()
      }
    }
  }
</script>
<style scoped>
  .handle-box {
    margin-bottom: 20px;
  }
  
  .handle-select {
    width: 120px;
  }
  
  .handle-input {
    width: 300px;
    display: inline-block;
  }
  
  .del-dialog-cnt {
    font-size: 16px;
    text-align: center
  }
  
  .table {
    width: 100%;
    font-size: 14px;
  }
  
  .red {
    color: #ff0000;
  }
</style>



